  <div>
    <v-navigation-drawer v-model="drawer" absolute app stateless>
      <v-list class="pa-1">
        <v-list-tile avatar>
          <v-list-tile-avatar>
            <img src="https://randomuser.me/api/portraits/men/85.jpg">
          </v-list-tile-avatar>

          <v-list-tile-content>
            <v-list-tile-title>{{$store.state.user.username}}</v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
      </v-list>

      <v-list class="pt-0" dense>
        <v-divider></v-divider>shop

        <v-list-tile
          v-for="item in items"
          :key="item.title"
          v-bind:to="item.to"
          @click="drawer=!drawer"
        >
          <v-list-tile-action>
            <v-icon>{{ item.icon }}</v-icon>
