    <v-toolbar color="teal " dark app flat>
      <v-toolbar-side-icon @click="drawer=!drawer"></v-toolbar-side-icon>

      <v-toolbar-title>
        Sure
        <b>Chat</b>
      </v-toolbar-title>

      <v-spacer></v-spacer>

      <v-btn icon to="/">
        <v-icon>home</v-icon>
      </v-btn>
      <v-menu
        bottom
        left
        offset-y
        :close-on-content-click="false"
        :min-width="300"
        :max-width="500"
        transition="slide-x-reverse-transition"
      >
        <template v-slot:activator="{ on }">
          <v-btn flat icon color="white" v-on="on">
            <v-badge v-if="$store.state.cart.length==0" color="grey" left overlap>
              <template v-slot:badge>
                <span>{{$store.state.cart.length}}</span>
              </template>
              <v-icon large icon>shopping_cart</v-icon>
            </v-badge>
            <v-badge v-else color="orange" left overlap>
              <template v-slot:badge>
                <span>{{$store.state.cart.length}}</span>
              </template>
              <v-icon large icon>shopping_cart</v-icon>
            </v-badge>
          </v-btn>
        </template>

        <v-list subheader three-line>
          <v-subheader>
            <div>Current Cart</div>
            <v-spacer></v-spacer>
            <v-btn v-if="$store.state.cart.length > 0" flat width="100%" @click="checkout">
              <v-icon color="teal">monetization_on</v-icon>Check out
            </v-btn>

            <v-btn v-else disabled flat width="100%">
              <v-icon color="teal">monetization_on</v-icon>Check out
            </v-btn>
          </v-subheader>
          <div v-for="(item, index) in $store.state.cart" :key="index">
            
              <!-- <v-list-tile-action @click="toggle_read(notifications.indexOf(item))">
              <v-btn flat icon>
                <v-icon title="Mark unread" :color="'primary'" v-if="item.read">done_all</v-icon>
                <v-icon title="Mark read" :color="'grey'" v-else>done</v-icon>
