<script>
import axios from "axios";
import Cookies from "js-cookie";

export default {
  data() {
    return {
      
      drawer: false,
      items: []
    };
  },
  methods: {
    getOrders: function() {
      this.$store.commit("GET_CART_UPDATE");
    },
    checkout: function() {
      let config = {
        headers: {
          "X-CSRFToken": Cookies.get("csrftoken")
        }
      };
      axios
        .post("api/store/order/checkout/", {}, config)
        .then(response => {
          this.$store.commit("GET_CART_UPDATE");
        })
        .catch(error => {
          console.log(error);
        });
    },
    getUserList: function() {
      let id;
      let user;
      let endpoint = "api/accounts/me/";
      axios
        .get(endpoint)
        .then(response => {
          id = response.data.id;
          user = response.data;
          
            this.items = [
              { title: "Calendar", icon: "calendar_today", to: "/calendar" },
              { title: "Messages", icon: "chat", to: "/chat" }
            ];
              
            
          
          this.items.push();
        })
        .catch(error => {
          console.log(error);
        });
    },
    logout: function() {
      window.location.replace(
        window.location.protocol +
          "//" +
          window.location.host +
          "/accounts/logout/"
      );
    },
    password: function() {
      window.location.replace(
        window.location.protocol +
          "//" +
          window.location.host +
          "/accounts/password/"
      );
    }
  },
  mounted() {
    
    this.getUserList();
  }
};
</script>

