import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    cart: [],
    user: {},
  },
  mutations: {
    GET_CART_UPDATE: (state) => {
      axios.get('api/store/order/').then(response => {
        state.cart = response.data
      }).catch(error => {
        console.log(error)
      })
    },
    GET_USER: (state) => {
      let endpoint = 'api/accounts/me/';
      axios.get(endpoint).then(response => {
        state.user.id = response.data.id;
        state.user.username = response.data.username;
        state.user.is_doctor = response.data.is_doctor;
        state.user.is_staff = response.data.is_staff;
        state.user.email = response.data.email;
      }).catch(error => {
        console.log(error)
      })
    }
  },
  actions: {

  },
  getters: {
    getCart: state => {
      return state.cart
    }
  },
})
